/**  Header Navigation **/

header .navbar {
  padding: 30px 0;
  transition: all 1s ease-out;
  // font-family: $font__roboto;
  font-size: 20px;
}

header .navbar-default {
  background-color: transparent;
  border: none;
}

header .navbar-default .navbar-nav a {
  color: gray;
  padding: 5px 8px;
}

header .navbar-default .navbar-nav a:hover {
  color: $color_navbar_state;
}

header .navbar-default .navbar-nav .active a {
  font-weight: 700;
  color: $color_navbar_state;
  background: transparent;
  border-bottom: 4px solid $color_navbar_state;
  text-shadow: none;
}

header .navbar-default .navbar-nav .active a:hover {
  color: #E15D5F;
  background: transparent;
  text-shadow: none;
}
@import "navbrand"; /**  Navbar Toggle **/

header .navbar-toggle {
  background-color: #279182;
}

header .navbar-default .navbar-toggle .icon-bar {
  background-color: white;
}

@media only screen and (max-width: 768px) {
  header .navbar-collapse.in {
    background-color: rgba(0,0,0, .5);
  }
}
/**  nav inbody **/
header .inbody {
  background: rgba(104,73,49,.9);
}

header .navbar.inbody {
  padding-top: 0;
  padding-bottom: 0;
}

header .navbar.inbody .navbar-nav {
  padding-top: 10px;
}

header .inbody .navbar-brand {
  background-size: 43px;
  background-position: top left;
  margin-top: 5px;
  padding-bottom: 0;
  margin-left: 10px;
}

header .inbody .navbar-brand h1 {
  font-size: 1.1em;
  padding-left: 38px;
}

header .inbody .navbar-brand span.subhead {
  display: none;
}
